import {createSlice} from "@reduxjs/toolkit";

  const resumeInfoReducer = createSlice({
    name:"resumeInfoReducer",
    initialState : {
        inforesume: {
            name: "",
            surname: "",
            dateofbirth: "01.06.2002",
            placeOfResidence: "city.Lviv",
            contacts: [
                {main: " : ", info: "+380971195151"},
                {main: " : ", info: "brainocswell@gmail.com"},
            ],
            hardAndSoftSkills: [
                {data: "   ,  , , ," +
                        "   ,   "},
                {data: "TypeScript, JS, HTML5, CSS3, bootstrap, React, Redux ect."}
            ],
            experiance: "  ",
            education: [
                {data: ":   . : "},
                {data: "Lgoos IT Academy"},
            ],
        }
    },
    reducers:{
        actionCreate(state,action){
            state.inforesume = action.payload
        },
    }
})

export default resumeInfoReducer.reducer
export const {actionCreate,changeName} = resumeInfoReducer.actions
